<template>
	<Tabs value="orderin" class="tab">
		<Tab-pane label="入库信息" name="orderin" class="tab-item">
			<div class="order-cell">
				<div class="cell-title">基本信息</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">入库申请编号：</span><span>{{info.gMId | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">贷款编号：</span><span>{{info.loanId | empty}}</span>
					</div>
				</div>
			</div>
			<div class="order-cell" v-for="item in info.mhList">
				<div class="cell-title">抵押品信息 -- （房）-- {{item.mHId}}</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">押品编号：</span><span>{{item.guaranteeNo | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">评估金额：</span><span>{{item.mhouseValue | fmoney | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">产权所有人：</span><span>{{item.propertyOwner | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">产权地址：</span><span>{{item.gAddress | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">房屋区域：</span><span>{{item.houseArea | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">建筑面积：</span><span>{{item.acreage | empty}}m²</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">权证类型：</span><span>{{item.warrantType | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">权证号码：</span><span>{{item.propertyRightNumber | empty}}</span>
					</div>
				</div>
			</div>
			<div class="order-cell cell-end">
				<div class="loan-title">入库审批</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">备注说明：</span><span>{{info.outRemark | empty}}</span>
					</div>
				</div>
				<div class="beizhu" v-if="$route.params.refuse">
					<span class="order-item-title">拒绝原因：</span>
					<Input v-model="outRemark" class="text-box" type="textarea" :maxlength="100" :rows="4"
						   placeholder="请填写拒绝原因（100字以内）..."></Input>
				</div>
				<div class="btn-g">
					<i-button class="sub" type="primary" @click="submit">确定</i-button>
					<i-button class="cancel" type="ghost" @click="pageBack">取消</i-button>
				</div>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="pageBack">返回</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				info: {},
				outRemark: ''
			}
		},
		created: function () {
			this.loadInfo()
		},
		methods: {
			loadInfo: function () {
				let _this = this;
				let params = _this.$route.params;
				clearTimeout(timer);
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('Storage/searchInOutProduct', {
					loanId: params.loanId,
					serialNo:params.serialNo
				})
					.then(function (response) {
						let res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == '1') {
							_this.$Message.success({
								content: res.msg
							});
							_this.info = res.data;
						} else {
							_this.$Message.error({
								content: res.msg,
								duration: 3
							});
							_this.$router.go(-1);
						}
					})
					.catch(function (error) {
						//处理错误
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Message.error({
							content: error,
							duration: 3
						});
						_this.$router.go(-1);
					});
			},
			submit: function () {
				var _this = this;
				if (_this.info.loanId == undefined || _this.info.loanId == '' || _this.info.loanId == null) {//贷款编号是否为空
					_this.$Message.error('贷款编号错误!')
					_this.$router.go(-1);
					return false
				} else {
					let params = _this.$route.params;
					if (params.refuse) {//拒绝
						_this.$http.post('/Storage/alterProductAccessory', {
							loanId: _this.info.loanId,
							status: 3,
							outRemark: _this.outRemark
						})
							.then(function (response) {
								if (response.data.code == 1) {
									_this.$Message.success({
										content: response.data.msg
									});
									_this.$router.push({name: 'pawnreview', params: {tab: 'in'}});
								} else {
									_this.$Message.error({
										content: response.data.msg,
										duration: 3
									});
								}
							})
							.catch(function (error) {
								_this.$Message.error({
									content: error,
									duration: 3
								});
							});
					} else {//同意
						_this.$http.post('/Storage/alterProductAccessory', {
							loanId: _this.info.loanId,
							status: 2
						})
							.then(function (response) {
								if (response.data.code == 1) {
									_this.$Message.success({
										content: response.data.msg
									});
									_this.$router.push({name: 'pawnreview', params: {tab: 'in'}});
								} else {
									_this.$Message.error({
										content: response.data.msg,
										duration: 3
									});
								}
							})
							.catch(function (error) {
								_this.$Message.error({
									content: error,
									duration: 3
								});
							});
					}
				}
			},
			pageBack: function () {//返回，取消
				var _this = this;
				let tab = _this.$route.params.tab;
				if (tab) {
					_this.$router.push({name: 'pawnreview', params: {tab: tab}})
				} else {
					_this.$router.go(-1);
				}
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.order-title {
		text-align: right;
		color: #343434;
		font-size: 14px;
	}

	.order-cell {
		padding-bottom: 5px;
		border-bottom: 1px solid #e5e5e5;
		margin-bottom: 30px;
	}

	.cell-end {
		border-bottom: none;
	}

	.cell-title {
		height: 14px;
		border-left: 3px solid #ff8226;
		color: #ff8226;
		font-size: 14px;
		line-height: 14px;
		padding-left: 10px;
		margin-bottom: 30px;
	}

	.cell-body {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}

	.cell-item {
		min-width: 300px;
		padding-bottom: 15px;
		padding-right: 20px;
		color: #333333;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
	}

	.order-item-title {
		width: 100px;
		height: 22px;
		line-height: 22px;
		color: #7f7f7f;
		overflow: hidden;
	}

	.loan-title {
		width: 110px;
		height: 34px;
		line-height: 34px;
		background-color: #20abff;
		text-align: center;
		color: #fff;
		font-size: 14px;
		margin-bottom: 30px;
	}

	.beizhu {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
	}

	.text-box {
		width: 640px;
		resize: none;
	}

	.btn-g {
		width: 500px;
		padding-top: 100px;
		padding-bottom: 50px;
	}

	.sub {
		width: 200px;
		height: 50px;
		margin-right: 30px;
		background-color: #20abff;
		border-color: #20abff;
		font-size: 18px;
		letter-spacing: 5px;
	}

	.cancel {
		width: 200px;
		height: 50px;
		border-color: #20abff;
		color: #20abff;
		font-size: 18px;
		letter-spacing: 5px;
	}
</style>





